import { Button, FormControlLabel, Radio, RadioGroup, Typography } from "@mui/material";
import { Box } from "@mui/system";
import useForm from "../customHooks/useFrom";

const NotificationSettings = ({notificationSettings, changeSettings}) => {

    const {
        values,
        handleInputChange,
    } = useForm(notificationSettings);

    const handleSubmit = () => {
        changeSettings(values, 'notificationSettings')
    }

    return (
        <Box sx = {{width: '100%', height: '100%', padding: '30px 40px'}}>
            <Box sx = {{width: '100%', height: '100%'}}>
                <Typography sx = {{margin: '0 0 20px 0'}} variant="h6"> ""</Typography>
                <RadioGroup
                    aria-labelledby="demo-radio-buttons-group-label"
                    value={values.like}
                    name="like"
                    onChange={handleInputChange}
                    >
                    <FormControlLabel value="1" control={<Radio sx = {{padding: '0 10px'}}/>} label="." />
                    <FormControlLabel value="2" control={<Radio sx = {{padding: '0 10px'}}/>} label=" ,    " />
                    <FormControlLabel value="3" control={<Radio sx = {{padding: '0 10px'}}/>} label=" " />
                </RadioGroup>
                <Typography variant="subtitle2" sx = {{color: 'gray'}}>johnappleseed   .</Typography>
                <hr/>
                <Typography sx = {{margin: '0 0 20px 0'}} variant="h6"></Typography>
                <RadioGroup
                    aria-labelledby="demo-radio-buttons-group-label"
                    value={values.comments}
                    name="comments"
                    onChange={handleInputChange}
                    >
                    <FormControlLabel value="1" control={<Radio sx = {{padding: '0 10px'}}/>} label="." />
                    <FormControlLabel value="2" control={<Radio sx = {{padding: '0 10px'}}/>} label=" ,    " />
                    <FormControlLabel value="3" control={<Radio sx = {{padding: '0 10px'}}/>} label=" " />
                </RadioGroup>
                <Typography variant="subtitle2" sx = {{color: 'gray'}}>johnappleseed (-): " !"</Typography>
                <hr/>
                <Typography sx = {{margin: '0 0 20px 0'}} variant="h6">   </Typography>
                <RadioGroup
                    aria-labelledby="demo-radio-buttons-group-label"
                    value={values.subscriptionRequest}
                    name="subscriptionRequest"
                    onChange={handleInputChange}
                    >
                    <FormControlLabel value="1" control={<Radio sx = {{padding: '0 10px'}}/>} label="." />
                    <FormControlLabel value="2" control={<Radio sx = {{padding: '0 10px'}}/>} label=" " />
                </RadioGroup>
                <Typography variant="subtitle2" sx = {{color: 'gray'}}>John Appleseed (johnappleseed) (-)    .</Typography>
                <hr/>
                <Typography sx = {{margin: '0 0 20px 0'}} variant="h6"></Typography>
                <RadioGroup
                    aria-labelledby="demo-radio-buttons-group-label"
                    value={values.reminders}
                    name="reminders"
                    onChange={handleInputChange}
                    >
                    <FormControlLabel value="1" control={<Radio sx = {{padding: '0 10px'}}/>} label="." />
                    <FormControlLabel value="2" control={<Radio sx = {{padding: '0 10px'}}/>} label=" " />
                </RadioGroup>
                <Typography variant="subtitle2" sx = {{color: 'gray'}}>        .</Typography>
                <hr/>
                <Typography sx = {{margin: '0 0 20px 0'}} variant="h6"> </Typography>
                <RadioGroup
                    aria-labelledby="demo-radio-buttons-group-label"
                    value={values.firstPublications}
                    name="firstPublications"
                    onChange={handleInputChange}
                    >
                    <FormControlLabel value="1" control={<Radio sx = {{padding: '0 10px'}}/>} label="." />
                    <FormControlLabel value="2" control={<Radio sx = {{padding: '0 10px'}}/>} label=" ,    " />
                    <FormControlLabel value="3" control={<Radio sx = {{padding: '0 10px'}}/>} label=" " />
                </RadioGroup>
                <Typography variant="subtitle2" sx = {{color: 'gray'}}>    johnappleseed  Instagram,     .</Typography>
                <hr/>
                <Typography sx = {{margin: '0 0 20px 0'}} variant="h6"> </Typography>
                <RadioGroup
                    value={values.supportRequests}
                    name="supportRequests"
                    onChange={handleInputChange}
                    >
                    <FormControlLabel value="1" control={<Radio sx = {{padding: '0 10px'}}/>} label="." />
                    <FormControlLabel value="2" control={<Radio sx = {{padding: '0 10px'}}/>} label=" " />
                </RadioGroup>
                <Typography variant="subtitle2" sx = {{color: 'gray'}}>    (:  10)    .</Typography>
                
                <Box 
                    sx = {{
                        margin: '20px 0', 
                        width: '100%', 
                        display: {xs: 'block', sm: 'block', md: 'flex'}, 
                        justifyContent: 'left',
                    }}
                >
                    <Button
                        onClick={handleSubmit}
                        variant="contained" 
                        sx = {{
                            display: 'block',
                            margin: {xs: '20px', sm: '20px', md: '0'}
                        }}
                    >
                        
                    </Button>
                </Box>
            </Box>
        </Box>
    )
}

export default NotificationSettings