import { Box, Button, Checkbox, FormControlLabel, Typography } from "@mui/material";
import useForm from "../customHooks/useFrom";

const SecuritySettings = ({securitySettings, changeSettings}) => {

    const {
        values,
        handleCheckboxChange,
    } = useForm(securitySettings);

    const handleSubmit = () => {
        changeSettings(values, 'securitySettings')
    }    

    return (
        <Box sx = {{width: '100%', height: '100%', padding: '30px 40px'}}>
            <Box sx = {{width: '100%', height: '100%'}}>
                <Typography sx = {{margin: '0 0 20px 0'}} variant="h6"> </Typography>
                <FormControlLabel 
                    id = 'accountPrivacy'
                    control={<Checkbox 
                        name = 'accountPrivacy'
                        onChange = {handleCheckboxChange}
                        checked = {values.accountPrivacy}
                    />} 
                    label=" "/>
                <Typography variant="subtitle2" sx = {{color: 'gray'}}>    ,      Instagram    ,   .       .</Typography>
                <hr/>
                <Typography sx = {{margin: '20px 0'}} variant="h6"> </Typography>
                <FormControlLabel  
                    id = 'onlineStatus'
                    control={<Checkbox 
                        name = 'onlineStatus'
                        onChange = {handleCheckboxChange}
                        checked = {values.onlineStatus}
                    />} 
                    label="  "/>
                <Typography variant="subtitle2" sx = {{color: 'gray'}}>  ,    ,  ,    , ,      Instagram   .   ,        .</Typography>
                <Box 
                    sx = {{
                        margin: '20px 0', 
                        width: '100%', 
                        display: {xs: 'block', sm: 'block', md: 'flex'}, 
                        justifyContent: 'left',
                    }}
                >
                    <Button
                        onClick={handleSubmit}
                        variant="contained" 
                        sx = {{
                            display: 'block',
                            margin: {xs: '20px', sm: '20px', md: '0'}
                        }}
                    >
                        
                    </Button>
                </Box>
            </Box>
        </Box>
    )
}

export default SecuritySettings